ờng, Quạt thông gió công nghiệp….\n                ")]),_vm._v(" "),_c('div',[_vm._v("\n                  Sau 10 năm nỗ lực không ngừng, luôn đáp ứng mọi nhu cầu về vòng bi của khách hàng với nguyên tắc phục vụ đặc biệt,  luôn cung cấp hàng chính hãng-phục vụ không giới hạn không gian và thời gian. Trong khi đó giá sản phẩm luôn thấp hơn nhiều các đơn vị danh tiếng khác, giúp khách hàng tiết kiệm ngân sách trong sản xuất mà chất lượng không đổi. GIB Việt Nam đã may mắn trở thành đối tác tin cậy của hàng loạt các tập đoàn lớn.\n                ")])])])])],1)],1)],1)],1),_vm._v(" "),_c('div',{staticClass:"row whyBox",staticStyle:{"padding":"30px 0","background-color":"#003586"}},[_vm._m(0),_vm._v(" "),_c('v-container',[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-4 col-md-4 col-sm-12 col-xs-12 _item"},[_c('div',{staticClass:"icon _icon1"},[_c('v-img',{staticClass:"white--text align-end",attrs:{"contain":"","src":require('../assets/img/home/why-icon-1.png')}})],1),_vm._v(" "),_c('div',{staticClass:"info"},[_c('h2',{staticClass:"title",attrs:{"itemprop":"name"}},[_c('i',{staticClass:"fas fa-caret-square-right",staticStyle:{"color":"#38c500","padding-right":"5px"}}),_vm._v("Online - Offline")]),_vm._v(" "),_c('div',{staticClass:"desc",attrs:{"itemscope":"","itemprop":"acceptedAnswer","itemtype":"https://schema.org/Answer"}},[_c('span',{attrs:{"itemprop":"text"}},[_vm._v("\n                Sản phẩm được kinh doanh linh hoạt, Online và Offline. Bạn có thể mua hàng trực tiếp theo cách truyền thống hoặc Online, Giúp bạn tiết kiệm thời gian và tiền bạc.\n              ")])])])]),_vm._v(" "),_c('div',{staticClass:"col-4 col-md-4 col-sm-12 col-xs-12 _item"},[_c('div',{staticClass:"icon _icon1"},[_c('v-img',{staticClass:"white--text align-end",attrs:{"contain":"","src":require('../assets/img/home/why-icon-2.png')}})],1),_vm._v(" "),_c('div',{staticClass:"info"},[_c('h2',{staticClass:"title",attrs:{"itemprop":"name"}},[_c('i',{staticClass:"fas fa-caret-square-right",s